







































import Vue from 'vue';
import { Note, Notes, NOTES } from '@/utils/constants';
import {
  ScaleType,
  getScaleNotes,
  TriadChord,
  numOfScaleNotes,
  getChordFromScale,
  ChordNumber
} from '@/utils/scales';

export default Vue.extend({
  name: 'Scale',
  data(): {
    allNotes: Notes;
    homeNote: Note;
    scaleTypes: ScaleType[];
    scaleType: ScaleType;
  } {
    return {
      allNotes: NOTES,
      homeNote: 'C',
      scaleTypes: ['major', 'minor'],
      scaleType: 'major'
    };
  },
  computed: {
    notes(): Note[] {
      return getScaleNotes(this.homeNote, this.scaleType);
    },
    chords(): TriadChord[] {
      const chords: TriadChord[] = [];

      for (let i = 0; i < numOfScaleNotes; i++) {
        chords.push(
          getChordFromScale(
            this.homeNote,
            this.scaleType,
            (i + 1) as ChordNumber
          )
        );
      }

      return chords;
    }
  }
});
